import handleHeader from './goodsHeader.vue'
import interfaces from '../../utils/interfaces.js'
import popupSpec from '../../components/popupSpec.vue'
export default {
	components:{
	   handleHeader,
	   "popup-spec": popupSpec //这个是弹框组件
	},
	data() {
		return {
			goodsInfo:{
				name: "",
				price: "",
				number: 1,
				spec: ""
			},
			goodsData: {
				swiperList: [],
				spec: [],
				comment: [{face:"",content:"",username:""}],
				descriptionStr: ""
			},
			currentSwiper: 0,//轮播图下标
			spaceInfo:{
				showSpace: false
			},
			isKeep:false //收藏
		}
	},
	onLoad(option) {
		this.initData()
		this.goodsInfo = JSON.parse(option.goodsInfo)//每一个点击商品列表进入的详情页 数据
		console.log(this.goodsInfo)
	},
	methods:{
		// 请求的接口
		initData() {
			this.request({
				url:interfaces.getGoods,
				success:((res) =>{
					console.log(res.data)
					this.goodsData = res.data
				})
			})
		},
		// 轮播图的小标
		changeSwiper(event){
			// console.log(event.detail.current)
			this.currentSwiper = event.detail.current
		},
		handleRatings() {
			// 本地存储
			uni.setStorageSync('comments',this.goodsData.comment)
			//页面跳转
			uni.navigateTo({
				url: "./ratings"
			})
		},
		keep(){
			this.isKeep = !this.isKeep;
			if(this.isKeep)
				uni.showToast({
					icon:'success',
					title:'已收藏'
				})
		},
		// 加入购物车
		joinCart(){ 
		//存储到本地存储里
		 //1.先到本地存储里取
		 uni.getStorage({
		 	key:"goodsList",
			success:(res =>{
				//拿数据
			   let goodsList = res.data;
			   // 查找商品是否存在
			   let isExist = false;
			   // 遍历判断是否是同一个产品或是商品规格是否发生变化
			   goodsList.forEach(goods => {
				   if(goods.goods_id == this.goodsInfo.goods_id && goods.spec == this.goodsInfo.spec) {
					   //如果存在 修改商品数量
					   goods.number += this.goodsInfo.number;
					   isExist = true
				   }
			   })
			   if (!isExist) {
				   goodsList.push(this.goodsInfo);
				   //更新本地存储
				   this.setGoodsList(goodsList)
			   }
			}),
			fail: (err => {//2.没有得到数据，那就存
			    console.log("加入存储")
				let goodsList = [];
				goodsList.push(this.goodsInfo)
				
				//3.往本地存储中存储数据
				this.setGoodsList(goodsList);
			})
		 })
		},
		//往本地存储中存储数据  方法
		setGoodsList(goodsList) {
			console.log("存储到本地存储中")
			//存储到本地存储中
			uni.setStorage({
				key: "goodsList",
				data: goodsList,
				success: () => {
					uni.showToast({
						icon: "success",
						title: "添加购物车成功"
					})
				}
			})
		},
		buy(){ // 立即购买
		
		}
	}
}
